.subtotal{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 300px;
	height: 100px;
	padding: 20px;
	background-color: white;
	border: 1px solid #dddddd;
	border-radius: 3px;
	color: black;
}
.subtotal_gift{
	display: flex;
	align-items: center;
}
.subtotal_gift>input{
	margin-right: 5px; 
}

.subtotal >button{
	background-color: #ffdcba;
	border-radius: 2px;
	width: 100%;
	height: 30px;
	border: 1px solid;
	margin-top: 10px;
	border-color: #a88734 #9c7e31 #846a29;
	color: black;
}
.subtotal > button:hover{
	background-color: #755139FF;
	color: white;
	transition: 0.5s;
}